
.offers{

    width: 75%;
    height: 60vh;
    display: flex;
    margin-top: 50px;
    margin-left: 100px;
    padding: 20px 14px;
    background: linear-gradient(180deg, #fde1ff,#e1ffea22 60%);

}
.offer-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    padding-top: 20px;
    padding-left: 20px;
    justify-content: center;
}
.offer-left h1{
    color: #171717;
    font-size: 60px;
    font-weight: 600;
    padding-left: 25px;
}
.offer-left p{
    color: #171717;
    font-size: 15px;
    font-weight: 600;
    padding-top: 5px;
    padding-left: 25px;

}
.offer-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: #fde1ff;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}
.offer-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    padding-top: 50px;

}